	module.exports = {"data":{"nimblRenderedHtml":{"html":"<p>Si ispira a Schopenhauer e ai suoi studi di filologia greca.<br>\nLa sua idea fondamentale è che <mark>il criterio di giudizio delle filosofie non sia la verità, ma se sono salutari</mark>, ossia se esprimono la volontà di potenza.</p>\n<p>Nietzsche tende a far filosofia per aforismi o brevi paragrafetti architettati per soprendere e provacare stupore, con giudizi taglienti.</p>\n<h1 id=\"spirito-apollineo-e-dionisiaco\">Spirito apollineo e dionisiaco</h1>\n<p>Nietzsche indivuda 2 spiriti nella grecità classica:</p>\n<ul>\n<li><strong>dionisiaco</strong>: porta l'uomo alla distruzione ed è il primo ad apparire storicamente</li>\n<li><strong>apollineo</strong>: è lo spirito della misura e dell'ordine appare successivamente</li>\n</ul>\n<p>L'uomo ha paura del dionisiaco e quindi sceglie l'apollineo.</p>\n<h1 id=\"su-verita-e-menzogna-in-senso-extramorale\">Su verità e menzogna in senso extramorale</h1>\n<p>L'uomo crede di essere speciale perché intelligente, ma <mark>l'intelligenza è solo un mezzo per la conservazione</mark>.</p>\n<p>L'uomo si accorda con gli altri uomini perpoter vivere in comunità di avere la stessa visione in modo da limitare il più brutale <em>bellum omnium contra omnes</em> (Hobbes).<br>\n<mark>La pulsione dell'uomo verso la verità in realtà è verso l'autoconservazione</mark>.</p>\n<p>La realtà è un chaos e ci siamo inventati la verità per cercare di riordinarlo, ma ci riusciamo solo in parte.<br>\n<mark>La verità quindi è mentire tutti quanti nello stesso modo, mentire non è immorale</mark>.</p>\n<p>La menzogna viene percepita dalla società come un pericolo e quindi essa bandisce colui che mente. La truffa non viene punita perché truffa in sè, ma per il danno che arreca.</p>\n<blockquote>\n<h2 id=\"frase-da-tatuare\">Frase da tatuare</h2>\n<p>Che cos'è dunque la verità? Un esercito mobile di metafore, metonimie, antropomorfismi, [...] le verità sono illusioni, delle quali si è dimenticato che appunto non sono che illusioni</p>\n</blockquote>\n<p><mark>Gli uomini hanno idolatrato la verità</mark> e ne hanno fatto qualcosa di straordinario, il problema è che questo meccanismo di difesa ci fa vivere male.</p>\n<h1 id=\"considerazioni-inattuali\">Considerazioni inattuali</h1>\n<p>Nietzsche crede di non essere espressione del suo tempo (per questo inattuali).</p>\n<h2 id=\"considerazione-sulla-utilita-e-il-danno-della-storia-per-la-vita\">Considerazione sulla utilità e il danno della storia per la vita</h2>\n<p>Analizza il modo in cui la società occidentale ha concepito la storiografia:</p>\n<ul>\n<li><strong>storia monumentale</strong>: grandi imprese ed eroi. È negativa se ci facciamo bloccare nell'azione ipnotizzati dalla loro grandezza, altrimenti è positiva</li>\n<li><strong>storia antiquaria</strong>: celebrare le prorie radici. Può essere positivo e sentimentale, ma può anche bloccarci nel passato</li>\n<li><strong>storia critica</strong>: si pone in maniera critica al passato. Può essere utile se ci serve a riconoscere gli errori del passato, ma negativa se ci impedisce di cogliere i nostri legami con il passato.</li>\n</ul>\n<p><mark>La storia è utile se ci permette di essere attivi nel presente.</mark></p>\n<h1 id=\"periodo-illuminista\">Periodo illuminista</h1>\n<p>Durante il suo periodo illuminista Nietzsche vuole <strong>rischiarare la cultura</strong> e i valori nella società.<br>\nDurante questo periodo scrive 3 opere:</p>\n<ul>\n<li><strong>Umano, troppo umano</strong></li>\n<li>Gaia scienza</li>\n<li>Genealogia della morale</li>\n</ul>\n<p>Paul Ricoeur, filosofo francese, dice che Nietzsche sottopone le certezze dell’occidente alla <strong>scuola del sospetto</strong>: tutti i valori dell’Occidente (morale, bontà, misericordia) vanno sottoposti a un’indagine. Questo implica di essere inattuali. <mark>I valori non valgono di per sé, ma vengono fatti valere dall’uomo.</mark></p>\n<p>Ricouer accosta a Nietzsche anche Marx e Freud: sono tutti i pensatori che hanno insegnato a sospettare e ad andare dietro le apparenze. Marx sospetta che dietro la democrazia parlamentare ci sia un rapporto di dominio di classe. Freud mostra che la nostra coscienza è soltanto una piccola zona della mente e in realtà dietro di essa vi sono delle movitazioni irrazionali e inconfessabili che ci muovono.</p>\n<h1 id=\"umano-troppo-umano\">Umano troppo umano</h1>\n<p><mark>La vita è illogica</mark>, ma noi tentiamo inutilmente di far venire fuori la logica.</p>\n<p>Inizialmente ci sono uomini che sono uguali in forza e che si distruggerebbero a vicenda combattendo, quindi si accordano per scambiarsi qualcosa: questa è la <strong>giustizia</strong>. La giustizia è dunque compensazione e scambio che parte dal pressuposto di forze uguali.</p>\n<p>È sorta poco a poco la parvenza che un'azione giusta sia un'azione altruistica, che è andata a sostituire la giustizia originale. Dato che l'altruismo è un valore difficile da raggiungere è diventato anche più prezioso.<br>\nQuesta nuova giustizia è perversa, perché non serve all'autoconservazione.</p>\n<p><mark>Di fronte alla morale non dobbiamo chiederci se sia giusta o sbagliata</mark>, bensì da dove venga, in che condizione i valori si siano sviluppati (analisi genealogica della morale).<br>\nE.g. Il cristianesimo secondo Nietzsche ha prodotto una morale degli schiavi.</p>\n<p>Esistono 2 tipi di morali:</p>\n<ul>\n<li><strong>morale dei signori</strong>: la legge del più forte, è la migliore, perché aiuta la vita</li>\n<li><strong>morale degli schiavi</strong>: siamo tutti uguali, è contro natura</li>\n</ul>\n<p>Per Nietzsche non solo alcuni, ma <mark>tutti i valori non hanno valore</mark>, da cui l'annuncio &quot;Dio è morto&quot;, ossia è morta l'origine di tutti i valori.<br>\nFino ad ora l'uomo ha acquisito i valori da fuori, adesso ognuno deve crearsi una propria morale.</p>\n<p><strong>Wille zur Macht</strong>: volontà di potenza, è la forza vitale. Simile alla volontà di Schopenhauer, che però è un espressione della nostra individualità.</p>\n<p><strong>Ubermensch</strong>: un uomo che non è schiavo di volontà esterne, che non riconosce limiti alla sua volontà di potenza. Traducibile come superuomo o oltreuomo.</p>\n<h1 id=\"cosi-parlo-zaratustra\">Così parlò Zaratustra</h1>\n<p>Dopo aver fatto filosofia con il martello e distrutto tutto è ora di ricreare qualcosa.</p>\n<p><strong>Zaratustra</strong> è un saggio che viaggia per il mondo in un percorso spirituale che rappresenta le varie tappe del superuomo che porta il suo messaggio nel mondo.</p>\n<h2 id=\"le-tre-metamorfosi\">Le tre metamorfosi</h2>\n<p>Il cammino della coscienza dagli idoli della superstizione e dalle menzogne della morale al dionisiaco e al superuomo viene raffigurato da Nietzsche in 3 tappe:</p>\n<ol>\n<li><strong>cammello</strong> che rappresenta l'uomo che si piega davanti alla maestà di Dio, lo spirito crede che la sua realizzazione sia sottoporre sé stesso a tutte le privazioni gravose, anteporre gli altri a sé</li>\n<li><strong>leone</strong> (&quot;Io voglio&quot;) che reagisce e combatte e sconfigge il drago (&quot;Tu devi&quot;), che rappresenta i valori tradizionali, il leone però vive solo per contrapporsi hai valori tradizionali e ciò non ha senso, dobbiamo arrivare al ...</li>\n<li><strong>fanciullo</strong> che esprime l'essenza dionisiaca della libertà umana, è un nuovo inizio, dopo aver abbattuto la tradizione il fanciullo è in grado di creare qualcosa di nuovo. Il fanciullo è l'ubermensch</li>\n</ol>\n<h2 id=\"eterno-ritorno\">Eterno ritorno</h2>\n<p>Dottrina sul tempo: <mark>il tempo è ciclico</mark>, ossia torneranno <strong>infinite</strong> volte gli eventi che stiamo vivendo.<br>\nÈ il pensiero più profondo, perché costringe a diventare superuomini o morire sotto questo pensiero.</p>\n<p>Il fatto che rivivremo questo momento infinite volte rende insopportabile agire in maniera che non affermi la volontà di potenza.</p>\n<p>Scarta quindi la storia cristiana, con la venuta di Cristo alla fine, e la storia comunista, con il mondo socialista come punto di arrivo.</p>\n<h3 id=\"pensiero-non-ubermensch\">Pensiero non-ubermensch</h3>\n<p>La creazione acquista significato al momento del disvelamento, la fine dei tempi. Agisco per un fine futuro.</p>\n<p>E.g. Sono pio adesso per entrare in paradiso, non sperpero per avere più soldi in futuro.</p>\n<h3 id=\"pensiero-ubermensch\">Pensiero ubermensch</h3>\n<p>Il tempo è un cerchio, di conseguenza <mark>le mie azioni devono avere senso in sé</mark>, perché non ci sarà un momento del disvelamento.</p>\n<p>L'idea del tempo ciclico non è una dottrina scientifica, ma un test esistenziale: come vivresti se fosse così?</p>\n<p>L'altruismo è negativo, devi agire solo in modo da elevare la tua esistenza.</p>\n<h2 id=\"la-visione-e-lenigma\">La visione e l'enigma</h2>\n<p>Zaratustra sta salendo su per un monte, ma viene trattenuto da un nano, lo spirito di gravità. Si fermano di fronte ad una porta carraia (attraverso cui passa una strada). Zaratustra dice al nano: &quot;guarda questa porta, due sentieri conducono a essa il passato e il futuro che sono due eternità infinite indietro e in avanti. La porta è il presente&quot;.</p>\n<p>Zaratustra domanda al nano se secondo lui questi due sentieri si incontreranno mai?<br>\n<mark>Il nano riconosce che il tempo è un cerchio, ma Zaratustra ritiene che il nano non abbia compreso le implicazioni di ciò.</mark></p>\n<p>Per far comprendere al nano l'importanza di ciò racconta la storia di un pastore con un serpente nella bocca (simbolo dell'eterno ritorno), per liberarsene lo morde e dopo non è più lo stesso, è diventato un ubermensch.</p>\n<p>Di fronte all'eterno ritorno potremmo essere tentati di ritenere che la nostra vita sia già accaduta e non abbiamo controllo sulla nostra vita, quello che vuole qui dire è che dobbiamo prendere una forte decisione e non saremo più in pericolo, ci trasformeremo in ubermensch.</p>\n<h1 id=\"nichilismo\">Nichilismo</h1>\n<p>Con <strong>nichilismo</strong> si intende l'idea che i valori siano nulla. Così come di fronte all'idea dell'eterno ritorno il superuomo reagisce considerandola un'occasione di trovare senso in ogni azione da lui compiuta, alla stessa maniera di fronte alla notizia che non c'è Dio, che i valori sono nulla, ci sono 2 possibili atteggiamenti:</p>\n<ul>\n<li>nichilismo passivo (atteggiamento di Schopenhauer): depressione</li>\n<li>nichilismo attivo: constatare che <mark>ognuno deve creare i suoi valori</mark></li>\n</ul>\n","frontmatter":{"title":"Nietzsche","pdfCompletoPath":null,"draft":false},"headings":[{"value":"<p>Spirito apollineo e dionisiaco</p>\n","depth":1,"rawText":"Spirito apollineo e dionisiaco\r"},{"value":"<p>Su verità e menzogna in senso extramorale</p>\n","depth":1,"rawText":"Su verità e menzogna in senso extramorale\r"},{"value":"<p>Considerazioni inattuali</p>\n","depth":1,"rawText":"Considerazioni inattuali\r"},{"value":"<p>Considerazione sulla utilità e il danno della storia per la vita</p>\n","depth":2,"rawText":"Considerazione sulla utilità e il danno della storia per la vita\r"},{"value":"<p>Periodo illuminista</p>\n","depth":1,"rawText":"Periodo illuminista\r"},{"value":"<p>Umano troppo umano</p>\n","depth":1,"rawText":"Umano troppo umano\r"},{"value":"<p>Così parlò Zaratustra</p>\n","depth":1,"rawText":"Così parlò Zaratustra\r"},{"value":"<p>Le tre metamorfosi</p>\n","depth":2,"rawText":"Le tre metamorfosi\r"},{"value":"<p>Eterno ritorno</p>\n","depth":2,"rawText":"Eterno ritorno\r"},{"value":"<p>Pensiero non-ubermensch</p>\n","depth":3,"rawText":"Pensiero non-ubermensch\r"},{"value":"<p>Pensiero ubermensch</p>\n","depth":3,"rawText":"Pensiero ubermensch\r"},{"value":"<p>La visione e l'enigma</p>\n","depth":2,"rawText":"La visione e l'enigma\r"},{"value":"<p>Nichilismo</p>\n","depth":1,"rawText":"Nichilismo\r"}]}},"pathContext":{"category":"Filosofia","title":"Nietzsche"}}

